body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --bold-bold-12: Roboto;

  /* font sizes */
  --bold-bold-12-size: 12px;
  --black-black-10-size: 10px;
  --bold-bold-13-size: 13px;
  --bold-bold-18-size: 18px;
  --black-black-8-size: 8px;

  /* Colors */
  --gray-blue-grey-blue-90: #dadee6;
  --gray-blue-grey-blue-98: #f7f8fa;
  --gray-blue-grey-blue-97: #f5f6f7;
  --gray-blue-grey-blue-95: #edeff2;
  --white-white: #fff;
  --gray-blue-grey-blue-60: #6b7a99;
  --gray-blue-grey-blue-50: #4d5e80;
  --gray-blue-grey-blue-70: #7d8fb3;
  --black-black-50: rgba(0, 0, 0, 0.5);
  --blue-blue: #3361ff;
  --green-green: #29cc39;
  --gray-blue-grey-blue-85: #c3cad9;
  --yellow-yellow: #ffcb33;
  --pink-pink: #e62e7b;

  /* Gaps */
  --gap-4xl: 23px;
  --gap-xl: 20px;
  --gap-mini: 15px;
  --gap-3xs: 10px;
  --gap-8xs: 5px;
  --gap-16xl: 35px;
  --gap-6xl: 25px;
  --gap-36xl: 55px;
  --gap-2xl: 21px;

  /* Paddings */
  --padding-mini: 15px;
  --padding-xl: 20px;
  --padding-8xs: 5px;
  --padding-6xl: 25px;
  --padding-7xs: 6px;
  --padding-3xs: 10px;
  --padding-11xl: 30px;
  --padding-16xl: 35px;

  /* border radiuses */
  --br-3xs: 10px;
  --br-8xs: 5px;
  --br-mini: 15px;
  --br-81xl: 100px;
  --br-6xs: 7px;

  /* Effects */
  --shadows-gray-blue-30-3-5b: 0px 2px 5px rgba(38, 51, 77, 0.03);
}
